@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.ofertas{
    margin-bottom: v.$separacion;
    //min-height: 45vh;
    h1{
        margin: 5rem 0;
    }
}

.btn-volver{
    @include m.boton(v.$azul, inline-block);
}

.btn-ver-mas{
    @include m.boton(v.$asignar, block);
    width: fit-content;
    margin: v.$separacion auto;
    &:hover{
        background-color: v.$azulClaro;
        transform: scale(1.1)
    }
}

.btn-vacante{
    @include m.boton(v.$asignar, block);
}

.vacantes-index{
    padding: math.div(v.$separacion, 2) 0;
    margin: math.div(v.$separacion, 2) auto;
    position: relative;
    // background-color: lighten(v.$azulFondo, 9);
    &::before {
        background-color: v.$azulFondo;
        content: "";
        display: block;
        height: 65%;
        position: absolute;
        bottom: 0;
        width: 100%;
        z-index: -1;
    }

    h2{
        font-size: 4rem;
        position: relative;
        font-weight: v.$black;
        // color: v.$azulClaro;
        &::after {
            background-color: v.$asignar;
            content: "";
            display: block;
            height: 0.3rem;
            left: 12%;
            position: absolute;
            bottom: 0;
            width: 70vw;
        }
    }

    span{
        text-transform: capitalize;
    }
}

.ofertas-nav{
    margin-bottom: v.$separacion;
    nav{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: flex-start;
        justify-items: center;
        gap: v.$separacion;

        a{
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .iconos{
            width: 75%;
            background: v.$asignar;
            text-align: center;
            padding: v.$separacion 0;
            box-shadow: 3px 3px 5px #707070;
            border-radius: 1rem;
            height: 100%;
            & p{
                font-size: 1.8rem;
                margin: 0;
                color: v.$blanco;
                text-transform: capitalize;
            }

            .fa{
                color: v.$blanco !important;
                
                font-size: 2.2rem !important;
            }

            &:hover{
                cursor: pointer;
                transform: scale(1.02);
                background-color: v.$verde;
            }
            @include m.tablet{
                display: flex;
                justify-content: center;
                align-items: center;
                & p{
                    padding: 0 1rem;
                    font-size: 3rem;
                }
                .fa{
                    font-size: 2.2rem !important;
                }
            }
        }
    }
}

.vacantes{
    margin: v.$separacion auto;
    span{
        text-transform: capitalize;
    }
    .sin-ofertas{
        background-color: v.$gris;
        gap: v.$separacion;
        width: 70%;
        margin: auto;
        margin-top: v.$separacion;
        padding: 1.5rem 3rem;
        border-radius: 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        i, p{
            color: v.$blanco;
            font-weight: v.$bold;
            text-align: center;
        }
        i{
            font-size: 8rem;
        }
        p{
            font-size: 4rem;
        }
    }
}

.grid-vacantes{
    @include m.grid(5, 5rem);    
}

.grid-index-v{
    @include m.grid(4, 5rem);
    width: 70%;
    margin: auto;
}
.vacante-index{
    background-color: v.$blanco;
}

.vacante{
    font-size: 0px;
    display: flex;
    flex-direction: column;
    img{
        object-fit: cover;
        // width: 100%;
        // height: 25rem;
    }
    p{
        padding: 1rem;
        margin: auto;
        span{
            font-weight: v.$medium;
            font-size: 2.2rem;
        }
    }
    a{
        font-size: 1.8rem;
    }
    &:hover>.btn-vacante{
        background-color: v.$azulClaro;
    }
}

.top{
    h1{
        text-transform: capitalize;
    }
}

.info-oferta{
    padding: v.$separacion;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgba(255, 255, 255, 0.85);
    background-blend-mode: lighten;
    margin-bottom: v.$separacion;
    
    @include m.grid(2, 0);
    place-items: center;

    img{
        max-width: 50rem;
        box-shadow: 5px 5px 5px v.$gris;
    }
    p{
        font-size: 2.2rem;
        font-weight: v.$medium;
        text-transform: capitalize;
    }
    span{
        font-weight: v.$bold;
        color: v.$asignar;
        text-transform: capitalize;
    }
}