@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;


.mensajes{    
    margin: v.$separacion 0;
    .grid-inbox{
        background-color: lighten(v.$gris, 9);
        display: grid;
        grid-template-columns: 1fr 1fr 2fr 3fr 1fr;
        border: 1px solid v.$gris;
        padding:1rem;
        border-radius: .5rem;
        font-weight: v.$bold;
        align-items: center;
    }
    .leido{
        background-color: darken(v.$gris, 9) !important;
        font-weight: v.$medium !important;
    }
}

.nav-mensaje{
    display: flex;
    justify-content: space-between;
}

.body-mensaje{
    background-color: v.$azulFondo;
    border-radius: 1rem;
    padding: 1rem 3rem;
    margin: v.$separacion 0;
    p{
        font-weight: v.$bold;
        color: v.$asignar;
        span{
            font-weight: v.$medium;
            color: v.$negro;
            padding-left: 1rem;
        }
    }
}