@use "sass:math";
@use 'variables' as v;
@use 'mixins' as m;

.btn-contacto{
    @include m.boton(v.$asignar, block);

    @include m.tablet{
        margin: 0 auto math.div(v.$separacion, 2) auto;      
        width: 25%;        
    }
    &:hover{
        background-color: v.$verde;
    }
}

.btn-contacto-servicios{
    @include m.boton(v.$asignar, block);
    margin: 2rem auto;
    padding: 1rem v.$separacion;
    font-size: 1.4rem;
    @include m.tablet{
        margin: 2rem v.$separacion 2rem 0;
        padding: 1rem 0.7rem;
    }
    @include m.desktop{
        margin: 2rem v.$separacion;
        padding: 1rem 3rem;
        font-size: 1.6rem;
    }
    @include m.giant{
        margin: v.$separacion v.$separacion *3;
        font-size: 2.8rem;
    }
    &:hover{
        background-color: v.$verde;
    }
}

.btn-enviar{
    @include m.boton(v.$verde, block);
    border-radius: 2rem;
    margin-inline-start: auto;
}
.btn-enviar-empleado{
    @include m.boton(v.$azulClaro, block);
    border-radius: 2rem;
    margin: auto;
}