@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta{
    padding: .7rem;
    text-align: center;
    font-weight: v.$bold;
    color: v.$blanco;
    text-transform: uppercase;
    margin: 1rem 0;
    
    &.error{
        background-color: #722323;
    }

    &.exito{
        background-color: #63c23d;
    }
}

.admin{
    margin-bottom: v.$separacion;
    h2{
        margin-top: 5rem;
    }
    section{
        display: none;
        &.vertabla{
            display: inline;
        }
    }
}

.btn-admin{
    @include m.boton(v.$azul, inline-block);
}

.btn-actualizar{
    @include m.boton(#63c23d, block);
}

.btn-eliminar{
    @include m.boton(#722323, block);
}

.admin-nav{
    nav{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: flex-start;
        justify-items: center;
        gap: v.$separacion;

        .iconos{
            width: 80%;
            background: v.$asignar;
            text-align: center;
            padding: 0.5rem 1rem;
            box-shadow: 3px 3px 5px #707070;
            border-radius: 1rem;
            & p{
                font-size: 1.8rem;
                margin: 0;
                color: v.$blanco;
            }
            .fa{
                color: v.$blanco !important;
                margin: 0.7rem;
                font-size: 2.2rem !important;
            }

            &:hover{
                cursor: pointer;
                transform: scale(1.02);
                background-color: v.$verde;
            }
        }
    }
}

//Estilos tablas del Admin
table.admin{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;
    padding: 1rem;

    tr{
        box-shadow: 0px 2px lighten(v.$asignar, 8);
    }

    td{
        padding: 1rem 0;

        & a{
            margin-top: 1rem;
        }
    }


    thead{
        background-color: v.$asignar;

        th{
            color: v.$blanco;
            padding: 2rem;
        }
    }

    .imagen-tabla{
        width: 12rem;
        border-radius: 1rem;
        box-shadow: 3px 3px 3px #969696;
        margin: auto;
    }
}
.imagen-small{
    width: 16rem;
}

.tab-verde{
    background-color: v.$verde !important;
}