@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.preguntas-bloque{
    img{
        width: 20%;
        margin: auto;
    }
}
.preguntas-contenedor{
    display: flex;
    flex-direction: column;    
    .pregunta{
        background-color: v.$amarillo;
        width: 80%;
        margin: 1rem 0;
        padding: 0 2rem;
        &:nth-child(odd){
            align-self: flex-start;
            border-radius: 0 10rem 10rem 0;
        }
        &:nth-child(even){
            align-self: flex-end;
            border-radius: 10rem 0 0 10rem;
        }        
        p{
            text-align: center;
        }
    }
}