@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;


.form-admin{
    border-radius: 1rem;
    width: 75%;
    margin: 2rem auto;
    background-color: #EAEAEA;
    border: 0.2rem solid v.$asignar;
    padding: 5rem 2rem;

    fieldset{
        border: none;
    }

    legend{
        font-size: 2.4rem;
        color: v.$asignar;
        margin-bottom: 1.5rem;
        font-weight: v.$black;
    }

    label{
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
        & span{
            text-transform: capitalize;
            color: lighten(v.$grisOscuro, 9);
        }
    }
    input:not([type="submit"], [type="checkbox"]){
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$asignar;
        border-radius: 1rem;
        margin-bottom: 2rem;
        caret-color: v.$asignar;
    }

    select, textarea{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$asignar;
        border-radius: 1rem;
        margin-bottom: 2rem;
        caret-color: v.$asignar;
    }


    .btn-form{
        @include m.boton(v.$asignar, block);
    }
    .password{
        display: flex;
        input[type="checkbox"]{
            margin-right: 1rem;
        }
    }
}