@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.grid-corporativo{
    margin-bottom: 4rem;
    @include m.tablet{
        display: grid;
        grid-template-columns: 35% 65%;
    }
    .div-corporativo{
        img{
            height: 50rem;
            object-fit: cover;
        }
    }
    .info-corporativo{
        margin-left: v.$separacion;
        display: flex;
        align-items: flex-start;
        gap: math.div(v.$separacion, 2);
        .div-info{
            .fa{
                font-size: 5rem;
                color: v.$blanco;
                background-color: v.$azulClaro;
                padding: 2rem;
            }
            h1{
                background-color: v.$azulClaro;
                margin-top: 0;
                color: v.$blanco;
                padding: 1.5rem;
                border-radius: 0 v.$separacion 0 0;
            }
            p{
                border: 3px solid v.$azulClaro;
                padding: 5rem;
                border-radius: 0 0 v.$separacion v.$separacion;
            }
        }
    }
}

.interes{
    padding: v.$separacion;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: v.$verde;
    margin-bottom: 4rem;
    h2{
        color: v.$blanco;
        width: 70%;
    }
}